.skills {
  background-image: url('../assets/images/skills_bg.png');
  @include backgroundImage(cover);
  width: 100%;
  height: 100%;
  z-index: -2;
  margin: -80px 0 0 0;
  padding-bottom: 150px;

  @include phoneScreen {
    background-image: url('../assets/images/skills_bg_mob.png');
    @include backgroundImage(cover);
  }

  &__title {
    @include gold;
    margin-top: 110px;

    @include phoneScreen {
      padding: 0 0 40px 0;
    }
  }

  &__container-body {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__left-block {

  }

  &__item {
    padding: 0 0 30px 0;

    &:nth-child(3) {
      padding-bottom: 60px;
    }
    &:nth-child(4) {
      padding-bottom: 50px;
    }
  }

  &__sub-title {
    background-image: linear-gradient(180deg, #8BC63E 0%, #666054 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-size: 45px;
    line-height: 78.4%;
    letter-spacing: 0.365em;
    text-transform: uppercase;
    margin-bottom: 10px;
    position: relative;

    .before {
      display: none;
    }

    @include ipadScreen {
      text-align: right;

      .before {
        display: block;
        position: absolute;
        top: 50%;
        background: linear-gradient(89.65deg, #8BC63E -33.74%, #666054 107.86%);
        height: 2px;
        width: 40px;
      }
    }

    .after {
      position: absolute;
      top: 50%;
      width: 80px;
      height: 2px;
      background: linear-gradient(89.65deg, #8BC63E -33.74%, #666054 107.86%);

      @include ipadScreen {
        display: none;
      }  
    }
  }

  &__text {
    @include gold;
    font-weight: 500;
    font-size: 18px;
    line-height: 155%;
    letter-spacing: 0.025em;

    @include ipadScreen {
      text-align: right;
    }
  }

  &__sub-box {
    padding-top: 15px;
  }

  &__sub-title-2 {
    @include gold;
    padding-bottom: 5px;
    font-size: 20px;
  }

  &__sub-text {
    @include gold;
    font-size: 18px;
    line-height: 148%;
    letter-spacing: 0.025em;
  }

  &__right-block {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include ipadScreen {
      display: none;
    }
  }

  &__first__img {
    
  }

  &__two__img {
  }

  &__thed__img {
  }
}
