.banner {
  background-image: url('../assets/images/banner.png');
  @include backgroundImage(100% 100%);
  width: 100%;
  padding: 170px 0 150px;
  
  @include tabletScreen {
    padding: 150px 0 150px;
  }

  @include ipadScreen {
    background-image: url('../assets/images/banner_ipad.png');
    @include backgroundImage(100% 100%);
    width: 100%;
  }

  @include phoneScreen {
    background-image: url('../assets/images/banner_phone.png');
    @include backgroundImage(100% 100%);
    width: 100%;
    padding: 120px 0 150px;
  }

  &__title {
    @include gold;
    font-style: italic;
    font-weight: bold;
    font-size: 58px;
    line-height: 160.4%;
    letter-spacing: 0.025em;
    text-transform: uppercase;

    
    @include tabletScreen {
      font-size: 50px;
      line-height: 111%;
      margin-bottom: 10px;
    }

    @include phoneScreen {
      text-align: center;
    }

    @include SEScreen {
      line-height: 111%;
      font-size: 40px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 18px;
    line-height: 160.4%;
    letter-spacing: 0.025em;
    max-width: 760px;
    @include gold;
    margin-bottom: 50px;

    @include phoneScreen {
      text-align: center;
      min-width: 100%;
    }
  }

  &__button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 400px;
    height: 65px;
    @include lightgreen;
    font-weight: bold;
    font-size: 18px;
    line-height: 160.4%;
    letter-spacing: 0.145em;

    @include ipadScreen {
      width: 300px;
    }

    @include phoneScreen {
      margin: 0 auto;
    }

    &::before, 
    &::after, 
    & span::before, 
    & span::after {
      content: '';
      position: absolute;    
      width: 60px;
      height: 20px;
      transition: .5s;

      @include ipadScreen {
        width: 30px;
        height: 18px;
      }
    }

    &::before {
      top: -0.5px;
      left: -0.5px;
      border-top: 2px color(lightgreen) solid;
      border-left: 2px color(lightgreen) solid;
    }

    &::after{
      top: -0.5px;
      right: -0.5px;
      border-top: 2px color(lightgreen) solid;
      border-right: 2px color(lightgreen) solid;
    } 
    
    & span::before{
      bottom: -0.5px;
      left: -0.5px;
      border-bottom: 2px color(lightgreen) solid;
      border-left: 2px color(lightgreen) solid;
    } 

    & span::after{
      bottom: -0.5px;
      right: -0.5px;
      border-bottom: 2px color(lightgreen) solid;
      border-right: 2px color(lightgreen) solid;
    }
    
    &:hover::before,
    &:hover::after,
    &:hover span::before,
    &:hover span::after{
      width: calc( 402px / 2 );
      height: calc( 70px / 2 );
    }
  }
}



