.footer {
  padding-bottom: 30px;

  &__container {
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 30px;
    border-bottom: 2px solid color(black);
    margin-bottom: 50px;

    @include tabletScreen {
      display: block;
    }

    @include ipadScreen {
      padding-bottom: 0px;
    }
  }

  &__logo {
    width: 150px;
  }

  &__menu {
    display: flex;

    @include ipadScreen {
      display: flex;
      flex-direction: column;
      margin-top: 20px;
    }
  }

  &__menu_list {
    margin-left: 60px;

    &:first-child {
      margin-left: 0;
    }

    @include tabletScreen {
      margin-left: 40px;
    }

    @include ipadScreen {
      margin: 0 0 20px 0;
    }
  }

  &__menu_link {
    @include black;
    font-weight: 500;
    font-size: 16px;
    letter-spacing: .08em;
    position: relative;
    transition: color 0.5s;

    &::before, &::after {
      position: absolute;
      font-size: inherit;
      font-weight: inherit;
    }

    &:hover, &.active {
      @include lightgreen;
      font-weight: 600;
  
      &::before {
        content: '<';
        left: -13px;
        @include lightgreen;
      }
  
      &::after {
        content: '/>';
        right: -21px;
        @include lightgreen;
      }
    }
  }

  &__social {
    display: flex;
    justify-content: space-between;

    @include tabletScreen {
      display: flex;
      flex-direction: column;
    }
  }

  &__social_item > a {
    display: flex;
    align-items: center;

    img {
      margin-right: 10px;
    }

    @include tabletScreen {
      margin-bottom: 20px;
    }
  }

  &__social_text {
    font-weight: 600;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: 0.08em;
    color: #626262;
    cursor: pointer;

    &:hover {
      @include black;
      transition: .3s;
    }

    a {
      font-weight: 600;
      @include black;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: color(black);
      }
    }
  }

  .popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(19, 28, 8, 0.9);
    backdrop-filter: blur(5px);
    top: 0;
    left: 0;
    z-index: 99;
    opacity: 0;
    overflow-y: auto;
    overflow-x: hidden;
    visibility: hidden;
    transition: all 0.4s;
    align-items: center;
    justify-content: center;
  
    &.open {
      opacity: 1;
      visibility: visible;
    }
  
    &__body {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 16px;
      height: 600px;
  
      @include phoneScreen{
        align-items: center;
      }
    }
  
    &__content {
      max-width: 400px;
      width: 100%;
      padding: 40px 24px 30px;
      opacity: 0;
      transform: perspective(600px) translate(0, -100%) rotateX(45deg);
      transition: all 0.4s;
      background: linear-gradient(136.3deg, #FFFFFF -17.07%, #FFFFFF 101.09%);
      box-shadow: 0px 4px 25px rgba(100, 100, 100, 0.95);
      border-radius: 10px;
      position: relative;
  
      @include phoneScreen{
        padding: 45px 16px 30px;
      }
    }
  
    &.open .popup__content {
      opacity: 1;
      transform: perspective(600px) translate(0px, 0%) rotateX(0);
    }
  
    .close-popup {
      position: absolute;
      top: 20px;
      right: 15px;
      cursor: pointer;

      @include phoneScreen {
        top: 10px;
        right: 10px;
      }

      img {
        width: 25px;
        height: 25px;

        @include phoneScreen {
          width: 20px;
          height: 20px;
        }
      }
    }
  
    &__heading{
      padding: 0;
      font-size: 25px;
      letter-spacing: 2px;
      font-weight: 700;
    }
  
    &__text{
      font-weight: 300;
      font-size: 14px;
      line-height: 18px;
      text-align: center;
      color: color(black);
    }
  
    form{
  
      input{
        width: 100%;
        padding: 8px;
        margin-top: 24px;
        border: none;
        border-bottom: 1px solid #BCBDC0;
        color: color(black);
        font-size: 14px;
  
        &::placeholder{
          color: #6C6D70;
        }
  
        &:focus{
          border-width: 2px;
          border-color: color(lightgreen2);
        }
  
        &.error{
          border-color: red;
        }

        @include phoneScreen {
          margin-top: 10px;
        }
      }
  
      .email-tel-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 24px;
  
        input{
          width: 100%;
        }
  
        @include phoneScreen{
          display: block;
  
          input{
            width: 100%;
          }
        }
      }
  
      textarea{
        width: 100%;
        height: 130px !important;
        padding: 8px;
        color: color(black);
        font-size: 14px;
        line-height: 22px;
        border: 1px solid #BCBDC0;
        margin-bottom: 25px;
        resize: none;
  
        &::placeholder{
          color: #6C6D70;
        }
  
        &:focus{
          border-width: 2px !important;
          border-color: color(lightgreen);
        }

        @include phoneScreen {
          height: 100px !important;
        }
  
        p{
          font-size: 11.3px;
          line-height: 17px;
          color: #BCBDC0;
        }
  
        @include phoneScreen{
          label{
            width: 88%;
            &::after {
              display: none;
            }
    
            &::before {
              content: '';
              bottom: 3px;
              right: -17%;
            }
          }
        }
      }

      .submit{
        width: 100%;
        height: 50px;
        @include black;
        font-size: 16px;

        &::before {
          top: -0.5px;
          left: -0.5px;
          border-top: 2px color(black) solid;
          border-left: 2px color(black) solid;
        }
    
        &::after{
          top: -0.5px;
          right: -0.5px;
          border-top: 2px color(black) solid;
          border-right: 2px color(black) solid;
        } 
        
        & span::before{
          bottom: -0.5px;
          left: -0.5px;
          border-bottom: 2px color(black) solid;
          border-left: 2px color(black) solid;
        } 
    
        & span::after{
          bottom: -0.5px;
          right: -0.5px;
          border-bottom: 2px color(black) solid;
          border-right: 2px color(black) solid;
        }
      }
    }
  }
  
  .success{
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(27, 35, 50, 0.6);
    backdrop-filter: blur(5px);
    z-index: 99;
  
    &.sended{
      display: block;
    }
  
    &__popup-wrapper{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 10px;
    }
  
    &__popup-content {
      max-width: 600px;
      width: 100%;
      height: 300px;
      padding: 30px 10px 0;
      background-color: color(white);
      margin: auto;
      border-radius: 20px;
  
      @include phoneScreen{
        height: 230px;
      }
  
    }
  
    &__content-text{
      margin-top: 30px;
      text-align: center;
      font-weight: 600;
      font-size: 30px;
      color: rgb(7, 136, 25);
  
      @include phoneScreen{
        margin-top: 10px;
        font-size: 23px;
      }
    }
  
    .success-checkmark {
      width: 80px;
      height: 115px;
      margin: 0 auto;
      
      .check-icon {
          width: 80px;
          height: 80px;
          position: relative;
          border-radius: 50%;
          box-sizing: content-box;
          border: 4px solid #4CAF50;
          
          &::before {
              top: 3px;
              left: -2px;
              width: 30px;
              transform-origin: 100% 50%;
              border-radius: 100px 0 0 100px;
          }
          
          &::after {
              top: 0;
              left: 30px;
              width: 60px;
              transform-origin: 0 50%;
              border-radius: 0 100px 100px 0;
              animation: rotate-circle 4.25s ease-in;
          }
          
          &::before, &::after {
              content: '';
              height: 100px;
              position: absolute;
              background: #FFFFFF;
              transform: rotate(-45deg);
          }
          
          .icon-line {
              height: 5px;
              background-color: #4CAF50;
              display: block;
              border-radius: 2px;
              position: absolute;
              z-index: 10;
              
              &.line-tip {
                  top: 46px;
                  left: 14px;
                  width: 25px;
                  transform: rotate(45deg);
                  animation: icon-line-tip 0.75s;
              }
              
              &.line-long {
                  top: 38px;
                  right: 8px;
                  width: 47px;
                  transform: rotate(-45deg);
                  animation: icon-line-long 0.75s;
              }
          }
          
          .icon-circle {
              top: -4px;
              left: -4px;
              z-index: 10;
              width: 80px;
              height: 80px;
              border-radius: 50%;
              position: absolute;
              box-sizing: content-box;
              border: 4px solid rgba(76, 175, 80, .5);
          }
          
          .icon-fix {
              top: 8px;
              width: 5px;
              left: 26px;
              z-index: 1;
              height: 85px;
              position: absolute;
              transform: rotate(-45deg);
              background-color: #FFFFFF;
          }
      }
  
      @keyframes rotate-circle {
        0% {
            transform: rotate(-45deg);
        }
        5% {
            transform: rotate(-45deg);
        }
        12% {
            transform: rotate(-405deg);
        }
        100% {
            transform: rotate(-405deg);
        }
    }
  
    @keyframes icon-line-tip {
        0% {
            width: 0;
            left: 1px;
            top: 19px;
        }
        54% {
            width: 0;
            left: 1px;
            top: 19px;
        }
        70% {
            width: 50px;
            left: -8px;
            top: 37px;
        }
        84% {
            width: 17px;
            left: 21px;
            top: 48px;
        }
        100% {
            width: 25px;
            left: 14px;
            top: 45px;
        }
    }
  
    @keyframes icon-line-long {
        0% {
            width: 0;
            right: 46px;
            top: 54px;
        }
        65% {
            width: 0;
            right: 46px;
            top: 54px;
        }
        84% {
            width: 55px;
            right: 0px;
            top: 35px;
        }
        100% {
            width: 47px;
            right: 8px;
            top: 38px;
        }
      }  
    }
  }
}