.about {

  &__container {
    position: relative;

    @include phoneScreen {
      margin: 0 0 130px 0;
    }
    
    &:before {
      content: '';
      position: absolute;
      background-image: url('../assets/images/bg_1.png');
      background-repeat: no-repeat;
      width: 100%;
      height: 100%;
      left: -40px;
      top: 170px;

      @include tabletScreen {
        display: none;
      }

      @include phoneScreen {
        display: none;
      }
    }

    &:after {
      content: '';
      position: absolute;
      background-image: url('../assets/images/bg_2.png');
      background-repeat: no-repeat;
      width: 100%;
      height: 100%;
      left: -40px;
      top: 640px;

      @include tabletScreen {
        display: none;
      }

      @include phoneScreen {
        display: none;
      }
    }
  }

  &__body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 180px;
    position: relative;

    @include tabletScreen {
      display: flex;
      flex-direction: column;

      &::after {
        content: '';
        position: absolute;
        background-image: url('../assets/images/bg_about_ipad.png');
        @include backgroundImage(contain);
        width: 100%;
        height: 100%;
        left: 0px;
        top: -44px;

        @include phoneScreen {
          display: none;
        }
      }
    }

    @include phoneScreen {
      padding-bottom: 0;

      &::before {
        content: '';
        position: absolute;
        background-image: url('../assets/images/bg_about_mob.png');
        @include backgroundImage(contain);
        width: 100%;
        height: 100%;
        left: 0px;
        top: -44px;
      }
    }
  }

  &__text {
    max-width: 700px;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.025em;
    @include black;
    padding: 15px 0 0 80px;

    @include tabletScreen {
      min-width: 100%;
      text-align: right;
      padding: 0;
    }

    @include phoneScreen {
      padding: 0;
      text-align: right;
      font-size: 16px;
      line-height: 150.4%;
      font-weight: 500;
      letter-spacing: 0.025em;
    }
  }

  &__bg {
    width: 500px;
    height: 500px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }

    @include tabletScreen {
      order: -1;
    }
  }
}

