
@mixin black {
  color: color(black);
}

@mixin white {
  color: color (white);
}

@mixin gold {
  color: color(gold);
}

@mixin lightgreen {
  color: color(lightgreen);
}



@mixin backgroundImage($bgSize: contain) {
    background-repeat: no-repeat;
    background-size: $bgSize;
}

@mixin lgScreen {
  @media screen and (min-width: screenWidth(lg)) {
    @content;
  }
}

@mixin tabletScreen {
    @media screen and (max-width: screenWidth(tablet)) {
      @content;
    }
}

@mixin ipadScreen {
  @media screen and (max-width: screenWidth(ipad)) {
    @content;
  }
}
  
@mixin phoneScreen {
  @media screen and (max-width: screenWidth(phone)) {
    @content;
  }
}


@mixin SEScreen {
  @media screen and (max-width: screenWidth(SE)) {
    @content;
  }
}
