*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html{
  scroll-behavior: smooth;
}

body {
  font-family: 'IBM Plex Mono', monospace;

  &.lock{
    overflow: hidden;
  }

  @include ipadScreen {
    overflow-x: hidden;
  }
}

ul{
  list-style: none;
}

a {
  text-decoration: none;
}

.title {
  text-align: center;
  font-style: italic;
  font-weight: bold;
  font-size: 70px;
  line-height: 160.4%;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #000000;
  padding: 30px 0;

  @include phoneScreen {
    padding: 20px 0 15px 0;
    letter-spacing: 0.3em;
    font-size: 45px;
    font-weight: 700;
  }
}

.wrapper {
  max-width: 1360px; 
  padding: 0 40px;
  margin: auto;
  overflow: hidden;

  // @include tabletScreen{
  //   max-width: 1104px;
  //   margin: 0 auto;
  // }

  @include ipadScreen{
    padding: 0 25px;
  }

  @include phoneScreen{
    padding: 0 15px;
  }

  @media (max-width: 411px) {
    padding: 0 8px;
  }
}