.contacts {
  background-image: url('../assets/images/contbgsection.png');
  @include backgroundImage(cover);
  width: 100%;
  margin-bottom: 100px;
  padding-bottom: 110px;

  @include ipadScreen {
    margin-bottom: 40px;
  }

  &__container {
    display: flex;
    justify-content: center;
    position: relative;
  }

  &__background {
    display: flex;
    align-items: center;
    position: absolute;
    top: 14%;
    z-index: 1;

    @include tabletScreen {
      left: 30px;
      top: 22%;
    }

    @include ipadScreen {
      left: 0;
      top: 22%;
    }

    @include phoneScreen {
      display: none;
    }
  }

  &__ferst_bg,
  &__second_bg {
    margin-right: 50px;
  }

  &__ferst_bg {
    
    @include tabletScreen {
      img {
        height: 800px;
      }
    }
  }

  &__second_bg {
    @include tabletScreen {
      img {
        height: 300px;
      }
    }
  }

  &__sethd_bg {
    @include tabletScreen {
      img {
        width: 90%;
      }
    }
  }

  &__forms {
    z-index: 2;
    padding: 200px 0 0 0;
  }

  &__title {
    @include gold;
    padding: 0;
  }

  &__text {
    @include gold;
    max-width: 800px;
    font-weight: 500;
    font-size: 18px;
    line-height: 165.5%;
    text-align: center;
    letter-spacing: 0.025em;
    margin-bottom: 50px;

    @include phoneScreen {
      text-align: left;
    }
  }

  &__form {
    text-align: center;
    padding-bottom: 70px;

    @include phoneScreen {
      padding-bottom: 0;
    }
  }

  &__input {
    background-color: transparent;
    border: none;
    border-bottom: 2px solid color(lightgreen);
    color: #757575;
    width: 550px;
    height: 50px;
    padding: 0 0 0 5px;
    font-weight: 500;
    font-size: 18px;
    line-height: 165.5%;
    letter-spacing: 0.025em;
    margin-bottom: 40px;

    &:focus {
      border: none;
    }
  }
  
  &__textarea {
    background-color: transparent;
    border: 2px solid color(lightgreen);
    color: #757575;
    width: 550px;
    height: 200px;
    padding: 10px 0 0 10px;
    font-weight: 600;
    font-size: 18px;
    line-height: 165.5%;
    letter-spacing: 0.025em;
    resize: none;
  }

  &__button {
    margin: 50px auto 0;
    width: 550px;

    &:hover::before,
    &:hover::after,
    &:hover span::before,
    &:hover span::after{
      width: calc( 556px / 2 );
      height: calc( 70px / 2 );
    }
  }

  @include phoneScreen {
    &__input,
    &__textarea,
    &__button {
      width: 100%;
    }
  }
}