.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(11px);
  z-index: 99;

  @include phoneScreen {
    padding: 10px 0;
  }

  .hader__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    width: 170px;
    height: 40px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }

    @include phoneScreen {
      width: 120px;
    }
  }

  &__menu {
    display: flex;
    justify-content: space-between;

    @include ipadScreen {
      position: absolute;
      left: 0;
      height: 100vh;
      top: 0;
      background: rgba(0, 0, 0, 1);
      backdrop-filter: blur(21px);
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 65%;
      padding: 100px 0 220px 0;
      transform: translateX(-100%);
      transition: transform 0.5s ease-in;
      z-index: -99;

      @include ipadScreen {
        padding: 150px 0 230px 0;
      }

      @include phoneScreen {
        padding: 80px 0 70px 0;
      }

      &.active {
        transform: translateX(0%);
      }
    }
  }

  &__list {
    margin: 0 60px 0 0;

    @include ipadScreen {
      opacity: 0;
      margin: 0;
    }

    &:last-child {
      margin: 0;

      // @include tabletScreen {
      //   margin: 0 0 0 20px;
      // }

      // @include ipadScreen {
      //   margin: 0;
      // }
    }
  }

  &__link {
    @include gold;
    font-weight: 500;
    font-size: 16px;
    letter-spacing: .08em;
    position: relative;
    transition: color 0.5s;

    &::before, &::after {
      position: absolute;
      font-size: inherit;
      font-weight: inherit;
    }

    &:hover, &.active {
      @include lightgreen;
  
      &::before {
        content: '<';
        left: -13px;
        @include lightgreen;
      }
  
      &::after {
        content: '/>';
        right: -21px;
        @include lightgreen;
      }
    }
  }
  
  &__lang {
    color: color(lightgreen);
    font-weight: 700;
  }

  &__mobile-menu {
    display: none;

    @include ipadScreen {
      display: block;
    }

    &.toggle {
      .header__line-1 {
        transform: rotate(-45deg) translate(-2px,4px);
      }
      .header__line-2 {
        transform: rotate(45deg) translate(-2px,-3px);
      }
    }
  }

  &__line-1, &__line-2 {
    width: 25px;
    height: 2px;
    margin: 5px;
    background-color: color(lightgreen);
    transition: all 0.3s ease;

    @include phoneScreen {
      width: 22px;
    }
  }

  @keyframes navLinkFade {
    from {
      opacity: 0;
      transform: translateX(-50px);
    }
    to {
      opacity: 1;
      transform: translateX(0px);
    }
  }

}